<template>
  <div>
    <editor
      id="id_ace"
      class="editor"
      v-model="acecontent"
      @init="editorInit"
      readonly="true"
      lang="latex"
      theme="chrome"
      :style="{'marginLeft':marginleft+'px'}"
    ></editor>
    <!-- <div @click="stopevent($event)" :style="{'marginLeft':marginleft+'px','paddingTop':'10px','paddingBottom':'10px'}"> -->
      <!-- <button class="copybtn" @click="copy2clipboard()">Copy to clipboard</button> -->
      <!-- <button class="downpic" @click="downloadpic()">Download Picture</button> -->
      <!-- <button class="downfile" @click="downtexfile()">Download latex file</button> -->
    <!-- </div> -->
  </div>
</template>

<script>

export default {
  name: "TexEditor",
  data() {
    return {
      marginleft: (window.innerWidth - 800) / 2,
      aceobj:null
    };
  },
  props: ["acecontent"],
  components: {
    editor: require("vue2-ace-editor")
  },
  mounted() {
    this.aceobj = window.ace.edit("id_ace")
  },
  methods: {
    stopevent: function(event) {
      event.stopPropagation();
    },
    editorInit: function() {
      require("brace/mode/html");
      require("brace/mode/latex");
      require("brace/theme/chrome");
    },
    copy2clipboard: function() {
      let editor = this.aceobj;
      let sel = editor.selection.toJSON(); // save selection
      editor.selectAll();
      editor.focus();
      document.execCommand('copy');
      editor.selection.fromJSON(sel);
    },
    downloadpic: function(){
      let picname = this.currentPicTag["name"];
      let a = document.createElement("a")
      a.href = "static/image/" + picname;
      a.download = picname
      document.body.appendChild(a);
      a.click()
      document.body.removeChild(a);
    },
    downtexfile: function(){
      this.saveTextAsFile(this.acecontent, "classviva-latex.tex")
    },
    saveTextAsFile: function(val, name){
      let textToSave = val;
      let  textToSaveAsBlob = new Blob([textToSave], {type:"text/plain"});
      let  textToSaveAsURL = window.URL.createObjectURL(textToSaveAsBlob);
      let  fileNameToSaveAs = name;
      let  downloadLink = document.createElement("a");
      downloadLink.download = fileNameToSaveAs;
      downloadLink.href = textToSaveAsURL;
      document.body.appendChild(downloadLink);
      downloadLink.click();
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style scoped>
.editor {
  margin-top: 60px;
  width: 800px !important;
  height: 700px !important;
}
.copybtn {
  margin-left: 60px;
  margin-right: 100px;
}
.downpic {
  margin-right: 100px;
}
.downfile {
  margin-right: 60px;
}
button{
  width: 160px;
  padding: 10px;
  border: 1px solid transparent;
  cursor: pointer;
  border-radius: 50rem !important;
  color: #fff;
  background-color: #007bff;
}

h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



