<template>
  <div>
    <PicGallery
      @requestTexid="getTexfile"
      :piclist="piclist"
    ></PicGallery>
    <div @click="hideEditor($event)">
      <TexEditor :acecontent="acecontent" v-if="showeditor" class="texEdi"></TexEditor>
    </div>
  </div>
</template>

<script>
import PicGallery from "./outer-product/PicGallery";
// import AppBase from "./outer-product/AppBase";
import TexEditor from "./outer-product/TexEditor";
import axios from 'axios';
export default {
  name: "Main",
  components: {
    PicGallery,
    TexEditor
    // AppBase
  },
  data() {
    return {
      piclist: [],
      data: [],
      options: [],
      filterparams: [],
      marginleft: 0,
      currentPage: 1,
      searchtag: "",
      showetagditor: false,
      showeditor: false,
      acecontent: "",
    };
  },
  computed: {
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll);
    this.updateLoadMore();
    axios
      .get('https://weihuan.tech/platex/static/json/data.json')
      .then(response => {
        this.data = response.data.data
        this.updateLoadMore()
      })
  },
  methods: {
    getTexfile(item) {
      axios.get('https://weihuan.tech/platex/static/tex/' + item["name"].replace(".png",".tex")).then(response => {
        this.refreshACEeditor(response.data)
      })
    },
    refreshACEeditor(data) {
      this.showeditor = true;
      document.getElementsByTagName("html")[0].style.overflow = "hidden";
      this.acecontent = data;
    },
    hideEditor(e) {
      if (e.target.className === "ace_content") {
      } else {
        this.showeditor = false;
        document.getElementsByTagName("html")[0].style.overflow = "auto";
      }
    },
    updateLoadMore() {
      let localdata = this.data
      if(this.piclist.length < localdata.length){
        this.piclist = localdata.slice(0, (this.piclist.length+20 > localdata.length)?localdata.length: (this.piclist.length+20))
      }
      this.stopfetch = false;
    },
    onScroll(e) {
      if (
        !this.stopfetch &&
        Math.abs(document.getElementsByTagName("html")[0].scrollHeight - (window.scrollY + window.innerHeight)) < 2
      ) {
        this.currentPage += 1;
        this.stopfetch = true;
        this.updateLoadMore();
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.stopscroll {
  overflow: hidden;
}
.startscroll {
  overflow: auto;
}
.texEdi {
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0px;
}
</style>



