.table {
  font-family: Lato, sans-serif;
  font-size: 1rem;
  font-weight: 400;
  margin-bottom: 2.5rem;
  line-height: 1.4;
}
.table__tr {
  border-bottom: 1px solid #e6e6e6;
  box-sizing: border-box;
  border-left: 0.25rem solid transparent;
}
.table__tr:nth-child(2n) {
  background: #fafafa;
}
.table__tr:hover {
  background: #f4f4f4;
}
.table__tr--primary {
  border-left: 0.25rem solid #41b883;
}
.table__tr--primary:hover {
  background: #f3fbf7;
}
.table__td,
.table__th {
  padding: 0.875rem 1rem;
}
.table__th {
  border-bottom: 1px solid #ccc;
}
.table__container {
  overflow-x: auto;
}
.table--full-size {
  width: 100%;
}
.table--fixed {
  table-layout: fixed;
}
.button {
  font-family: Lato, sans-serif;
  font-size: 0.875rem;
  font-weight: 400;
  color: #fff;
  min-height: 2.5rem;
  line-height: 1.4;
  padding: 0.5rem 1.875rem 0.625rem;
  box-sizing: border-box;
  position: relative;
  display: inline-block;
  background: #41b883;
  border: none;
  border-radius: 5px;
  z-index: 1;
  overflow: hidden;
  outline: none;
  vertical-align: middle;
  transition: background 0.1s ease, border-color 0.1s ease, color 0.1s ease;
  border-bottom: 3px solid #266d4d;
  text-decoration: none;
}
.button:focus,
.button:hover {
  background: #349268;
  cursor: pointer;
}
.button:active {
  background: #266d4d;
}
.button:focus {
  outline: none;
}
.button--small {
  padding: 0.375rem 1.25rem;
  font-size: 0.75rem;
  min-height: 1.875rem;
}
.button--xlarge {
  padding: 0.875rem 3.125rem 0.9375rem;
  font-size: 1.125rem;
  min-height: 3.75rem;
}
.button--large {
  padding: 0.6875rem 2.5rem 0.8125rem;
  font-size: 1rem;
  min-height: 3.125rem;
}
.button--fake {
  background: none;
  color: #374853;
  border-color: transparent;
}
.button--fake:focus,
.button--fake:hover {
  background: rgba(0, 0, 0, 0.05);
}
.button--hollow {
  background: none;
  color: #374853;
  border: 1px solid #a8a8a8;
}
.button--hollow:focus,
.button--hollow:hover {
  background: rgba(0, 0, 0, 0.05);
  color: #000;
  border: 1px solid #1c1c1c;
  cursor: pointer;
}
.button--expanded {
  width: 100%;
}
.button--secondary {
  background: #374853;
  border-bottom: 3px solid #0e1316;
}
.button--secondary:focus,
.button--secondary:hover {
  background: #232d34;
  cursor: pointer;
}
.button--success {
  background: #43ac6a;
  border-bottom: 3px solid #26633d;
}
.button--success:focus,
.button--success:hover {
  background: #358753;
  cursor: pointer;
}
.button--error {
  background: #f08a24;
  border-bottom: 3px solid #a3570b;
}
.button--error:focus,
.button--error:hover {
  background: #d3710e;
  cursor: pointer;
}
.button--info {
  background: #5fadd6;
  border-bottom: 3px solid #2a7ba5;
}
.button--info:focus,
.button--info:hover {
  background: #3698cc;
  cursor: pointer;
}
.button__group {
  margin-bottom: 20px;
  width: 100%;
}
.button__group .button {
  display: inline-block;
  margin-right: 0;
  margin-bottom: 0;
  border-radius: 0;
}
.button__group .button:first-child {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
.button__group .button:last-child {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}
.button__group--1 .button {
  width: 100%;
}
.button__group--2 .button {
  width: 50%;
}
.button__group--3 .button {
  width: 33.33333%;
