.form {
  position: relative;
}
.form__input,
.form__textarea {
  margin-bottom: 2rem;
  font-family: Lato, sans-serif;
  font-size: 0.875rem;
  font-weight: 400;
  color: #374853;
  line-height: 2.375rem;
  min-height: 2.375rem;
  position: relative;
  border: 1px solid #e8e8e8;
  border-radius: 5px;
  background: #fff;
  padding: 0 0.8125rem;
  width: 100%;
  transition: border 0.1s ease;
  box-sizing: border-box;
}
.form__input:hover,
.form__textarea:hover {
  border-color: #cfcfcf;
