.form__textarea--with-left-icon {
  padding-left: 2.8125rem;
}
.form__input--with-right-icon,
.form__textarea--with-right-icon {
  padding-right: 2.8125rem;
}
.form__icon {
  position: absolute;
  pointer-events: none;
  top: -1px;
  height: 2.5rem;
  line-height: 2.5rem;
}
.form__icon--right {
  right: 0.9375rem;
}
.form__icon--left {
  left: 0.8125rem;
}
