  color: #fff;
  border-color: #f7bd83;
  background: #f7bd83;
}
.form-group--alert input,
.form-group--alert input:focus,
.form-group--alert input:hover,
.form-group--alert textarea {
  border-color: #f7bd83;
}
.form-group--alert + .form-group__message {
  display: block;
  color: #f5b06c;
}
@keyframes b {
  0% {
    transform: translateX(0);
  }
  15% {
    transform: translateX(0.375rem);
  }
  30% {
    transform: translateX(-0.375rem);
  }
  45% {
    transform: translateX(0.375rem);
  }
  60% {
    transform: translateX(-0.375rem);
  }
  75% {
    transform: translateX(0.375rem);
  }
  90% {
    transform: translateX(-0.375rem);
  }
  to {
    transform: translateX(0);
  }
